import * as React from 'react';
import { ItemModel } from 'app/models/Items';
import { http } from 'app/utils/http';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ImageGallery from 'react-image-gallery';
import Collapse from 'reactstrap/lib/Collapse';
import Slider from 'react-slick';
import Item from '../ItemList/Item';


const styles = require('./style.css');
const itemsURL = '../../../assets/mockups/items.json';

interface State {
    selectedItem: ItemModel | null,
    images: any[] | null,
    isDescriptionOpen: boolean,
    relatedItemList: ItemModel[],
    isItemReady: boolean
}

interface OwnProps {
    itemId: any,
    history: any
}

type Props = OwnProps;

export class ItemDetails extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            selectedItem: null,
            images: null,
            isDescriptionOpen: false,
            relatedItemList: [],
            isItemReady: false
        }
    }

    async componentDidMount() {
        this.setSelectedItemDetails();
    }

    async componentDidUpdate() {
        if(this.state.selectedItem && this.state.selectedItem.id != this.props.history.location.pathname.split('/').pop() && this.state.isItemReady) {
            this.setState({
                isItemReady: false
            })
            window.scrollTo(0, 0);
            this.setSelectedItemDetails();
        }
        
    }

    setSelectedItemDetails = async () => {
        const res = await http.get(itemsURL);
        if(res.status === 200) {
            const selectedItem = res.data.items.find((item: ItemModel) => item.id == this.props.itemId)
            const images: any[] = [];
            images.push({original: selectedItem.bigImageUrl, thumbnail: selectedItem.bigImageUrl, thumbnailClass: styles.thumbnailClass, originalClass: styles.originalClass})
            selectedItem.morePictures.map((image: any) => {
                images.push({original: image, thumbnail: image, thumbnailClass: styles.thumbnailClass, originalClass: styles.originalClass})
            })
            this.setState({
                selectedItem,
                images,
                relatedItemList: res.data.items,
                isItemReady: true
            })
        } else {
            
        }
    }

    toggleDescription = () => {
        this.setState({
            isDescriptionOpen: !this.state.isDescriptionOpen
        })
    }

    handleOpenItemImage = () => {

    }


    render() {
        var settings = {
            dots: true,
            infinite: false,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 1
          };

        const fakeArray = [];
        const reviewAverate = this.state.selectedItem && this.state.selectedItem.reviewAverage || 0;
        for(let i=0; i<reviewAverate; i++) {
            fakeArray.push(1)
        }
        return (
            <>
            <div className={styles.itemDetailsContainer}>
                {this.state.selectedItem && this.state.isItemReady && 
                
                <div className={styles.item}>
                
                    <div className={styles.imageContainer}>
                        <div className={styles.image}>
                            <ImageGallery items={this.state.images} showPlayButton={false} stopPropagation={true} useBrowserFullscreen={true}
                            useTranslate3D={false} showBullets={true}
                            style={{maxHeight: '100vh', maxWidth: '100vw', objectFit: 'scale-down', fontFamily: "'object-fit: scale-down'"}}
                             />
                        </div>
                    </div>
                    <div className={styles.detailsContainer}>
                        <div className={styles.title}>
                            {this.state.selectedItem.title}
                        </div>
                        <div className={styles.reviews}>
                            <div className={styles.stars}>
                                {fakeArray.map((value, index) => <FontAwesomeIcon key={index} icon="star" color={'#eded34'} className={styles.heartIcon} />)}
                            </div>
                            <div className={styles.reviewNumber}>
                                ( {this.state.selectedItem.reviews.length} reviews )      
                            </div>
                        </div>
                        <div className={styles.price}>
                            <div className={styles.itemPrice}>
                                {this.state.selectedItem.price}$
                            </div>
                            <div className={styles.shippingPrice}>
                                (+{this.state.selectedItem.shippingPrice}$ shipping)
                            </div>
                            
                        </div>
                        <div className={styles.description}>
                            <div className={styles.header}>
                                <div className={styles.text} onClick={this.toggleDescription}>
                                    Description 
                                    <FontAwesomeIcon icon={['fab', 'ebay']} />
                                </div>
                                <div className={styles.arrow} onClick={this.toggleDescription}>
                                    {!this.state.isDescriptionOpen ? 
                                    <FontAwesomeIcon icon="angle-down" />
                                    :
                                    <FontAwesomeIcon icon="angle-up" />
                                    }
                                </div>
                                
                            </div>
                            <div className={styles.body}>
                            <Collapse isOpen={this.state.isDescriptionOpen}>
                                {this.state.selectedItem.description}
                            </Collapse>
                            </div>
                        </div>
                        <div className={styles.buyItNow}>
                            <div className={styles.button}>
                                Buy it Now!
                            </div>
                        </div>
                    </div>
                    <div className={styles.similarItems}>
                        <div className={styles.header}>
                            <div className={styles.text}>
                                Related Items
                            </div>
                        </div>
                        <div className={styles.body}>
                        <Slider {...settings}>
                            <div>
                                <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[0]} />
                            </div>
                            <div>
                            <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[1]} />
                            </div>
                            <div>
                            <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[2]} />
                            </div>
                            <div>
                            <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[4]} />
                            </div>
                            <div>
                            <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[5]} />
                            </div>
                            <div>
                            <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[6]} />
                            </div>
                        </Slider>
                        </div>
                    </div>
                </div>
                }
                
            </div>
            </>
        )
    }
}